import React, { useMemo, useState } from "react"
import { BriefConfig } from "@rp/react/tanstack-api/brief"
import { ActionsMenu } from "@rp/react/web/common/base-components"
import { Box } from "@rp/react/web/mui/core"
import { useBriefConfigCardActions } from "../../../hooks"

type BriefConfigActionsDangoProps = {
  briefConfig: BriefConfig | null
}

const BriefConfigCardActionsDango = ({ briefConfig }: BriefConfigActionsDangoProps) => {
  const { actionsDict } = useBriefConfigCardActions()
  const actions = useMemo(() => [actionsDict.edit, actionsDict.unsubscribe], [actionsDict])

  const [openMenu, setOpenMenu] = useState(false)

  if (!briefConfig) return null

  return (
    <Box sx={{ mr: "-8px" }}>
      <ActionsMenu open={openMenu} setOpen={setOpenMenu} actions={actions} item={briefConfig} />
    </Box>
  )
}

export { BriefConfigCardActionsDango }
